import React from 'react';
import classes from './Burger.css'
import BurgerIngredient from './BurgerIngredient/BurgerIngredient'

const burger = ( props ) => {
    /* const transformedIngredients = Object.keys(props.ingredients)
        .map(igKey => {
            return [...Array(props.ingredients[igKey])].map((_, i) => {
                return <BurgerIngredient key = {igKey + i} type = {igKey}/>
            })
        }); */

    /* const transformedIngredients = [
        <BurgerIngredient key = {'salad'} type = 'salad'/>,
        <BurgerIngredient key = {'bacon'} type = 'bacon'/>,
        [<BurgerIngredient key = {'cheese_1'} type = 'cheese'/>, <BurgerIngredient key = {'cheese_2'} type = 'cheese'/>],
        [<BurgerIngredient key = {'meat_1'} type = 'meat'/>, <BurgerIngredient key = {'meat_2'} type = 'meat'/>]
    ] */

    let transformedIngredients = Object.keys(props.ingredients)
        .map(igKey => Array.from(
            Array(props.ingredients[igKey]), 
            (_, i) => <BurgerIngredient key = {igKey + i} type = {igKey}/>)
        )
        .reduce((arr, el) => {
            return arr.concat(el)
        }, []); 

    if(transformedIngredients.length === 0) {
        transformedIngredients = <p>Please, start adding ingredients!</p>
    }

    return (
        <div className = {classes.Burger}>
            <BurgerIngredient type = "bread-top"/>
            {transformedIngredients}
            <BurgerIngredient type = "bread-bottom"/>

        </div>
    );
}

export default burger;